* {
  font-family: 'Nunito', 'Roboto Mono', monospace;
}

:root {
  --color-p: #b39ddb;
  --color-g: #89c4a7;
  --color-y: #d6c17a;
}

li {
  list-style-type: none;
}

#logo text{
  -webkit-user-select: none;
          user-select: none;
  font-family:'Roboto Mono';
  font-size:14px;
}

#logo tspan {
  font-family:'Nunito', monospace;
  font-size: 14px;
}

#logo .star {
  fill:var(--color-y);
}

text .tspan-0 {
  fill:var(--color-p);
}

text .tspan-1 {
  fill:var(--color-g);
}


#logo .broken{
  stroke-dasharray:8;
}

#logo .filled{
  fill:black;
}


#logo .end_marked_arrow{
  marker-end:url(#arrow);
}

#logo .start_marked_arrow{
  marker-start:url(#arrow);
}

#logo .end_marked_diamond{
  marker-end:url(#diamond);
}

#logo .start_marked_diamond{
  marker-start:url(#diamond);
}

#logo .end_marked_circle{
  marker-end:url(#circle);
}

#logo .start_marked_circle{
  marker-start:url(#circle);
}

#logo .end_marked_open_circle{
  marker-end:url(#open_circle);
}

#logo .start_marked_open_circle{
  marker-start:url(#open_circle);
}

#logo .end_marked_big_open_circle{
  marker-end:url(#big_open_circle);
}

#logo .start_marked_big_open_circle{
  marker-start:url(#big_open_circle);
}

.fade {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}


.animate-spin-slow {
  -webkit-animation: spin 10s linear infinite;
          animation: spin 10s linear infinite;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

#svgCont {
  height: 100%;
  width: 100%;
}

#svgCont:hover .animate-spin-slow {
  -webkit-animation: spin-reverse 5s linear infinite;
          animation: spin-reverse 5s linear infinite;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  cursor: pointer;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spin-reverse {
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes spin-reverse {
  100% {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

.hero .title, 
.hero .nickname,
.hero .subtitle {
  color: #d6c17a;
  text-align: center;
  margin: 0;
  padding: 0;
}
@-webkit-keyframes swing {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  50% { -webkit-transform: rotate(15deg); transform: rotate(15deg); }
  100% { -webkit-transform: rotate(-15deg); transform: rotate(-15deg); }
}
@keyframes swing {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  50% { -webkit-transform: rotate(15deg); transform: rotate(15deg); }
  100% { -webkit-transform: rotate(-15deg); transform: rotate(-15deg); }
}

.scrollable-iframe {
  overflow-y: scroll; /* Add vertical scrollbar */
  scrollbar-width: thin; /* Width of custom scrollbar */
  scrollbar-color: #888 /* Color of custom scrollbar */
}

.scrollable-iframe::-webkit-scrollbar {
  width: 0.5rem;
}

.scrollable-iframe::-webkit-scrollbar-track {
  background-color: #f1f1f1;
  border-radius: 1rem;
}

.scrollable-iframe::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 1rem;
}

.scrollable-iframe::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

.device-mockup {
  position: relative;
  display: inline-block;
  width: 80%;
  height: 0;
  padding-bottom: 45%;
  border: 1rem solid #33300000;
  border-radius: 1.5rem;
  overflow: hidden;

  opacity: 0.5; /* Set default opacity value */
  transition: opacity 0.5s ease; /* Add a transition effect */

  /* Define opacity values based on visibility */
  &.visible {
    opacity: 1;
  }

  &.visible + .device-mockup {
    opacity: 0.75;
  }

  &.visible + .device-mockup + .device-mockup {
    opacity: 0.5;
  }
}

.device-mockup.visible {
  opacity: 1;
  transition: opacity 0.5s ease; /* Add a transition effect */
}

.device-mockup.visible + .device-mockup {
  opacity: 0.75;
}

.device-mockup.visible + .device-mockup + .device-mockup {
  opacity: 0.5;
}

.device-mockup:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1.5rem;
  background-color: #6b6b6b;
  opacity: 0.25;
  transition: opacity 0.3s ease-in-out;
}

.device-mockup.visible:before,
.device-mockup.visible .toolbar-button,
.device-mockup.visible iframe {
  opacity: 1;
}


.device-mockup iframe {
  position: absolute;
  top: 1.5rem;
  left: 0;
  width: 100%;
  height: calc(100% - 1.5rem);
  border: none;
  opacity: 0.25;
  transition: opacity 0.3s ease-in-out;
}

/* Toolbar buttons */
.device-mockup .toolbar-button {
  position: absolute;
  width: 0.7rem;
  height: 0.7rem;
  border-radius: 50%;
  top: 0.35rem;
  opacity: 0.25;
  transition: opacity 0.3s ease-in-out;
}

.device-mockup .toolbar-button.red {
  background-color: #ff5f56;
  left: 0.5rem;
}

.device-mockup .toolbar-button.yellow {
  background-color: #ffbd2e;
  left: 1.75rem;
}

.device-mockup .toolbar-button.green {
  background-color: #27c93f;
  left: 3rem;
}

